import React from "react";
import icon from "./images/anime.jpg";
import "./styles/chrome.css";
import Topbar from "./Topbar";

function Chrome({ app }) {
  return (
    <div className="all_app" id="chrome_app" style={{ overflow: "hidden" }}>
      <Topbar
        app_id={"chrome_app"}
        title={"Animine"}
        window_id={app}
        icon={icon}
      />

      <iframe
        width="97%"
        height="100%"
        frameBorder="0"
        src="https://animine.netlify.app/"
        title="W3Schools Free Online Web Tutorials"
        id="browser"
        style={{
          outline: "none",
          marginTop: "15px",
          border: "none",
        }}
      ></iframe>
    </div>
  );
}

export default Chrome;
